.wt-native-select{
  position:relative;
}

.wt-native-select__select{
    position:absolute;
    top:0;
    left:0;

    opacity:0;

    width:100%;
    height:100%;
    -webkit-appearance:none;
       -moz-appearance:none;
            appearance:none;
  }

.wt-native-select__select_size_m{
    letter-spacing:normal;
    color:rgba(39, 40, 44, 0.70);
    font-family:system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Droid Sans', 'Helvetica Neue', Arial, sans-serif;
    font-size:15px;
    font-weight:normal;
    line-height:24px;
  }

.wt-native-select__select_size_s{
    letter-spacing:normal;
    color:rgba(39, 40, 44, 0.70);
    font-family:system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Droid Sans', 'Helvetica Neue', Arial, sans-serif;
    font-size:15px;
    font-weight:normal;
    line-height:24px;
  }

.wt-native-select__select_size_xs{
    letter-spacing:normal;
    color:rgba(39, 40, 44, 0.70);
    font-family:system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Droid Sans', 'Helvetica Neue', Arial, sans-serif;
